.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header > h2 {
  color: white;
  font-size: calc(10px + 5vmin);
  /* #9da8a3; */
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.story-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
}

/* Style for individual card */
.story-card {
  width: 100%; /* Set width to 100% for single card in a row */
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  display: flex;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  flex-direction: row; /* Arrange content vertically */
}

/* Style for card image */
.story-img {
  width: 200px; /* Set width for the image */
  height: 200px;
  background-size: cover;
  background-position: center;
  opacity: 0.8;
}

/* Style for card content */
.story-content {
  flex: 1;
  padding: 20px;
}

/* Style for card header */
.story-header {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 5px; /* Reduced margin */
  display: flex;
  flex-direction: column; /* Arrange content vertically */
}

/* Style for calendar icon */
.calendar-icon {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 12px;
  margin-right: 5px; /* Margin added here */
}

/* Style for card sub-header */
.story-subheader {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  color: #666;
}

/* Style for card paragraph */
.story-paragraph {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  color: #333;
  margin-top: 10px;
}

/* Media query for responsive layout */
@media screen and (max-width: 600px) {
  .story-card {
    width: 100%;
  }
  .story-img {
    width: 100%;
  }
}
